.idle { 
  height: 32px;
  width: 32px;
  -webkit-animation: idle steps(4);
          animation: idle steps(4); 
  -webkit-animation-duration: 0.4s; 
          animation-duration: 0.4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background-image: url('../assets/idle_cat.png');
}

@-webkit-keyframes idle {
  from { background-position:2px 0px; }
  to { background-position:128px 0px; }
}

@keyframes idle {
  from { background-position:2px 0px; }
  to { background-position:128px 0px; }
}


